.pageModule {
  /* height: 100%; */
  width: 100%;
  display: flex;
  /* align-items: center; */
  /* justify-content: center; */
  overflow-y: auto;
  /* overflow-x: auto; */
  flex-direction: column;
  padding: 20px;
}
.pageCenter {
  align-items: center;
  /* justify-content: center; */

  height: 100vh;
}
.downloadBtn a {
  text-decoration: none;
  color: #fff;
}
.mainSideBar {
  display: flex;
  height: calc(100vh - 64px);
  overflow-y: hidden;
}

.dataPage {
  /* height: 100%; */
  width: 100%;
  display: flex;
  padding: 25px;
  overflow-y: auto;
  overflow-x: hidden;
  flex-direction: column;
}
.dataPage :global(.MuiDataGrid-virtualScroller) {
  min-height: 40px;
  overflow-x: hidden;
}
.dataPage :global(.MuiDataGrid-overlayWrapperInner) {
  min-height: 40px !important;
}
.dataPage
  :global(
    .MuiDataGrid-row:not(.MuiDataGrid-row--dynamicHeight) > .MuiDataGrid-cell
  ) {
  justify-content: left;
}
.dataPage :global(.MuiDataGrid-columnHeader) {
  background-color: #ddd;
}
.fieldCorner :global(.MuiMenu-paper) {
  height: 300px;
}

.sideBarModule {
  display: flex;
  min-height: calc(100vh - 64px);
  background: url(./assets/ujjivan-u-logo.png) no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}
.sideMenuCard :hover {
  background-color: transparent;
}

