}
.updateInputLabel {
  font-size: 18px;
  font-weight: 700;
}
.noDetails {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.noDetailsInfo {
  display: flex;
  /* height: 100%; */
  width: 100%;
  /* align-items: center; */
  justify-content: center;
}
.userTopic {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.datePad {
  margin: 0px 30px 0 12px;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #837c7c;
  background: none;
