  border-left: none;
  border-bottom: 1px solid #837c7c;
  background: none;
  border-radius: unset;
}

/* //////////////////////////////////////////////Loader css///////////////////////////////////////////////////////////*/

.pageloader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.7);
}

.loader img {
  width: 70px;
  height: 70px;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: scale(0.9);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(0.9);
  }
}
/* //////////////////////////////////////////////Loader css///////////////////////////////////////////////////////////*/

/* test */
.failApp {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}
